<template>
    <div class="playing">
        <router-link class="logo" to="/" target="_blank">
            <span class="iconfont icon-custom-logo"></span>
            <span class="top">音乐Top</span>
        </router-link>
        <hr class="line">
        <div class="play_main">
            <div class="play_left">
                <div class="play_ing">
                    <div class="singsong">
                        <div class="sing_left">
                            <img :src="songsal.picUrl">  
                        </div>
                        
                        <div class="sing_right">
                            <h3 class="sing_h3">{{songsal.name}}</h3>
                            <h4 class="sing_h4">演唱：{{songssr.name}}</h4>
                        </div>
                    </div>
                    <audio class="player" :src="'https://music.163.com/song/media/outer/url?id='+singsongid+'.mp3'" autoplay controls></audio>
                </div>
                <div class="comments">
                    <h3 class="comments_h3">精彩评论</h3>
                    <div class="comments_con" v-for="(item,index) in hotComments" :key="index">
                        <div class="con_left">
                            <img :src="item.user.avatarUrl">
                        </div>
                        <div class="con_right">
                            <p class="con_p">
                                <span>{{item.user.nickname}}：</span>
                                {{item.content}}
                            </p>
                            <p class="con_p2">
                                <span class="iconfont icon-xihuan-01"></span>
                                ({{item.likedCount}})
                            </p>
                        </div>
                    </div>
                </div>
                <el-pagination
                background
                layout="prev, pager, next"
                :total="1000"
                class="el_page"
                @prev-click="prevpage"
                @current-change="curpage"
                :current-page="offset"
                @next-click="nextpage">
                </el-pagination>
            </div>
            <div class="play_right">
                <div class="song_list">
                    <h3 class="list_h3">相似歌单</h3>
                    <div v-for="(item,index) in playlists" :key="index">
                        <router-link class="list_a" :to="'/music/'+item.id">
                            <div class="list_con">
                                <img :src="item.coverImgUrl">
                                <div class="con_p">
                                    <p class="cons_p1">{{item.name}}</p>
                                    <p class="cons_p2">{{item.creator.nickname}}</p>
                                </div>
                            </div>
                        </router-link>
                    </div>
                </div>
                <div class="simi">
                    <h3 class="list_h3">相似音乐</h3>
                    <div @click="toggleMusic(item.id)" class="simi_con" v-for="(item,index) in songs" :key="index">
                        <div>
                            <p class="simi_p">{{item.album.name}}</p>
                            <p class="simi_p2">{{item.album.artists[0].name}}</p>
                        </div>
                        <span class="iconfont icon-bofang"></span>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            hotComments:[],
            offset:1,
            songsal:{},
            songssr:{},
            playlists:[],
            songs:[],
            singsongid:this.$route.query.id
        }
    },
    methods: {
        useraxios(httpUrl){
            return new Promise((resolve,reject)=>{
                 axios.post(httpUrl).then((res)=>{
                    resolve(res.data)
                })
                .catch((res)=>{
                    console.log(res)
                })
            })
            
        },
        nextpage:function(val){
            this.offset=val;
            this.useraxios(`http://musicapi.leanapp.cn/comment/music?id=${this.singsongid}&limit=20&offset=${this.offset*20}`)
            .then((data)=>{
                this.hotComments=data.comments;
            });
        },
        prevpage:function(val){
            this.offset=val;
            this.useraxios(`http://musicapi.leanapp.cn/comment/music?id=${this.singsongid}&limit=20&offset=${this.offset*20}`)
            .then((data)=>{
                this.hotComments=data.comments;
            });
        },
        curpage:function(val){
            this.offset=val;
            this.useraxios(`http://musicapi.leanapp.cn/comment/music?id=${this.singsongid}&limit=20&offset=${this.offset*20}`)
            .then((data)=>{
                this.hotComments=data.comments;
            });
        },
        toggleMusic:function(id){
            this.singsongid=id;
            this.offset=1;
            this.useraxios(`http://musicapi.leanapp.cn/comment/music?id=${this.singsongid}&limit=20&offset=${this.offset*20}`)
                .then((data)=>{
                    this.hotComments=data.comments;
                });
                this.useraxios(`http://musicapi.leanapp.cn/song/detail?ids=${this.singsongid}`)
                .then((data)=>{
                    this.songsal=data.songs[0].al;
                    this.songssr=data.songs[0].ar[0]
                });
                this.useraxios(`http://musicapi.leanapp.cn/simi/playlist?id=${this.singsongid}`)
                .then((data)=>{
                    this.playlists=data.playlists;
                });
                this.useraxios(`http://musicapi.leanapp.cn/simi/song?id=${this.singsongid}`)
                .then((data)=>{
                    this.songs=data.songs;
                });
        }
    },
    beforeMount() {
        this.useraxios(`http://musicapi.leanapp.cn/comment/music?id=${this.singsongid}&limit=20&offset=${this.offset*20}`)
        .then((data)=>{
            this.hotComments=data.comments;
        });
        this.useraxios(`http://musicapi.leanapp.cn/song/detail?ids=${this.singsongid}`)
        .then((data)=>{
            this.songsal=data.songs[0].al;
            this.songssr=data.songs[0].ar[0]
        });
        this.useraxios(`http://musicapi.leanapp.cn/simi/playlist?id=${this.singsongid}`)
        .then((data)=>{
            this.playlists=data.playlists;
        });
        this.useraxios(`http://musicapi.leanapp.cn/simi/song?id=${this.singsongid}`)
        .then((data)=>{
            this.songs=data.songs;
        });
    },   
}
</script>

<style lang="less" scoped>
@mainColor:#00B9F2;
a{
    text-decoration: none;
}
.logo{
    display: block;
    text-decoration: none;
    color: @mainColor;
    margin: 10px 0px 10px 50px;
    .iconfont{
        font-size: 30px;
    }
    .top{
        font-size: 24px;
        font-weight: 700;
    }
}
.line{
    width: 100%;
    border:1px solid @mainColor;
}
.play_main{
    width: 80%;
    max-width: 1368px;
    min-width: 1000px;
    margin: 0px auto;
    display: flex;
    .play_left{
        width: 70%;
        margin-bottom: 100px;
    }
    .play_right{
        width: 30%;
    }
}
.comments_h3{
    width: 80%;
    margin: 0px auto;
    border-bottom: 1px solid @mainColor;
    color: #666;
}
.comments_con{
    .comments_h3;
    border-bottom: 1px dashed @mainColor;
    padding-bottom: 10px;
    margin-top: 10px;
    display: flex;
    .con_left{
        width: 50px;
        height: 50px;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .con_right{
        width: 100%;
    }
}
.con_p{
    padding-left: 10px;
    font-size: 14px;
    span{
        color: @mainColor;
    }
}
.con_p2{
    text-align: right;
    font-size: 16px;
    margin-top: 10px;
}
.el_page{
    text-align: center;
    margin-top: 30px;
}
.singsong{
    width: 100%;
    height: 200px;
    display: flex;
    margin: 50px auto;
}
.sing_left{
    width: 200px;
    height: 200px;
    margin-left: 80px;
    border-radius: 50%;
    background: url(../assets/img/coverall.png) no-repeat;
    overflow: hidden;
    img{
        display:block;
        width: 150px;
        height: 150px;
        margin:25px;
        border-radius: 50%;
        background: gold;
        animation: imgdong 10s 3s infinite linear;
    }
}
@keyframes imgdong {
    0%{
        transform: rotate(0);
    }
    100%{
        transform: rotate(360deg);
    }
}
.sing_right{
    width: 300px;
    margin-left: 50px;

}
.sing_h3{
    width: 100%;
    text-align: center;
    font-family: "楷体";
    font-size: 28px;
    color: @mainColor;
    letter-spacing: 10px;
}
.sing_h4{
    .sing_h3;
    font-size: 16px;
    margin-top: 50px;
    letter-spacing: 5px;
}
.list_h3{
    width: 80%;
    margin: 50px auto;
    font-size: 14px;
    color: #666;
    padding-bottom: 10px;
    border-bottom: 1px solid @mainColor;
}
.list_a{
    width: 80%;
    margin: 20px auto;
    display: block;
}
.list_con{
    display: flex;
    img{
        width: 60px;
        height: 60px;
    }
}
.cons_p1{
    width: 150px;
    height: 30px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #000;
}
.cons_p2{
    .cons_p1;
    text-align: left;
    font-size: 12px;
    color: @mainColor;

}
.simi_con{
    width: 80%;
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    .iconfont{
        margin-right: 30px;
        color: @mainColor;
    }
    .simi_p{
        font-family: "楷体";
        font-weight: 700;
        font-size: 18px;
    }
    .simi_p2{
        .simi_p;
        font-size: 16px;
        color: #999;
        font-weight: normal;
    }
}
.player{
    width: 400px;
    margin:0px 0px 50px 50px;
    outline: none;

}
</style>